import ErrorType from './ErrorType';
import IDataError from '../dataAccess/IDataError';

/**
 * Represents the data error response
 */
class BaseDataError implements IDataError {
  public correlationId: string;
  public correlationDate: string;
  public errorMessage: string;
  public errorType: ErrorType;

  /**
   * Initializes a new instance of BaseDataError
   * @param correlationId associated with the error response
   * @param correlationDate associated with the error response
   * @param errorMessage associated with the error response
   * @param errorType associated with the error response
   */
  constructor(correlationId: string, correlationDate: string, errorMessage: string, errorType: ErrorType) {
    this.correlationId = correlationId;
    this.correlationDate = correlationDate;
    this.errorMessage = errorMessage;
    this.errorType = errorType;
  }
}

export default BaseDataError;
